<script>
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("videosModule");

export default {
  name: "videos",

  data() {
    return {
      mhlb: [],
      mhlbH: [],
      mhlbZ: [],
      mhlbR: [],
      mhlbS: [],
      mhlbW: []
    };
  },

  computed: {
    ...mapState(["cartoonboxD", "cartoonData"])
  },

  methods: {
    cartoonsGo(o) {
      this.$router.push(o);
    },

     particularsGo(o) {
      this.$router.push(o);
    }

  },

