    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });

    // 热门
    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?mhlb=hot"
    })
      .then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("videosModule/cartoonboxs", result);

        this.mhlb = result.config.url;
        // console.log("this.mhlb ==>> ", this.mhlb);

         this.$toast.clear();
      })
      .catch(err => {
        // console.log("err ==>> ", err);
        this.$toast.clear();
